.main_content_container {
    padding-left: 0px;
    display: flex;
    flex-direction: column;
    max-height: 100%;
    width: calc(100vw - 220px);
    position: relative;
}

.top_level_search {
    display: flex; 
    flex-direction: row;
    column-gap: 20px;
    height: 30px;
    width: 100%;
    justify-content: start;
    background-color: beige;
    padding-left: 10px;
}
.search_filtering {
    display: flex;
    flex-direction: column;
    height: 70px;
    justify-content: center;
    align-items: start;
    padding-left: 10px;
}
.filter_row_show {
    display: flex;
    padding-left: 20px;
    font-size: 10px;
    color:  blue;
}
.row_1_filtering {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
}
.row_2_filtering {
    display: flex;
    flex-direction: row;
    row-gap: 10px;
    height: 30px;
}
.filter_select {
    display: block;
    width: 200px;
}
