.main_content_container {
    padding: 10px;
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    max-height: 100%;
    width: calc(100vw - 220px);
    position: relative;
}
.pipeline_title {
    font-size: 20px;
    font-weight: bold;
}
.pipeline_tools {
    display: flex;
    column-gap: 15px;
    font-size: 18px;
}
.pipeline_tools > input {
    display: flex;
    column-gap: 10px; 
    font-size: 18px;
}
.pipeline_container {
    padding: 10px;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    height: 100%;
    width: 80vw;
}
.stage-element {
    min-width: 220px;
    max-width: 221px;
    padding:10px;
    margin-right: 10px;
    border-left: 1px solid lightgray;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    display: flex;
    flex-direction: column;

}
.stage-element:hover {
    background-color: aliceblue;
}
.stage-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}
.stage-title {
    color: darkslategrey;
    font-size: 16px;
}
.stage-fields {
    color: darkslategrey
}
.stage-body {
    color: slategrey;
    height: 75vh;
    overflow-y: auto;
}
.stage-footer {
    justify-self: end;
}
.opportunity-card {
    width: 170px;
    border: 1px solid lightgrey;
    padding: 15px;
    padding-top: 10px;
    border-radius: 4px;
    box-shadow: 0px 7px 15px -3px rgba(0,0,0,0.25);
    -webkit-box-shadow: 0px 7px 15px -3px rgba(0,0,0,0.25);
    -moz-box-shadow: 0px 7px 15px -3px rgba(0,0,0,0.25);
    background-color: white;
    font-size: 14px;
    margin-bottom: 5px;
}
.opportunity-card-title {
    color: darkslategrey;
    font-size: 15px;
    font-weight: 600;
}
.opportunity-card-id {
    font-size: 10px;
    margin-bottom: 5px;
}
.opportunity-props {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
    font-size: 10px;
    margin-bottom: 5px;
}
.opportunity-card-fields {
    font-size: 12px;
}
.strategic-prop {
    background-color: goldenrod;
    color: white;
    padding: 5px;
    border-radius: 20px;
    text-align: center;
}
.Sought-prop {
    background-color: goldenrod;
    color: white;
    padding: 5px;
    border-radius: 20px;
    text-align: center;
}
.Notice-prop {
    background-color: teal;
    color: white;
    padding: 5px;
    border-radius: 20px;
    text-align: center;
}
.Solicitation-prop {
    background-color: palevioletred;
    color: white;
    padding: 5px;
    border-radius: 20px;
    text-align: center;
}
.incumbent-prop {
    background-color: seagreen;
    color: white;
    padding: 5px;
    border-radius: 20px;
    text-align: center;
}
.stage-footer {
    opacity: 0;
    z-index: -1;
    transition: 0.4s;
}
.delete-stage-icon {
    border: 0px;
    padding: 5px;
    transition: 0.3s;
    border-radius: 5px;
    background-color: none;
}
.delete-stage-icon > svg {
    height: 20px;
    width: 20px;
    color: darkslategray;
    transition: 0.3s;
}
.configure-stage-icon {
    border: 0px;
    padding: 5px;
    transition: 0.3s;
    border-radius: 5px;
    background-color: none;
}
.configure-stage-icon > svg {
    height: 20px;
    width: 20px;
    color: darkslategray;
    transition: 0.3s
}
.configure-stage-icon:hover > svg {
    height: 22px;
    width: 22px;
    color: white;
}
.configure-stage-icon:hover {
    background-color: seagreen;
}
.stage-element:hover > .stage-footer {
    opacity: 1;
    z-index: 1;
}
.delete-stage-icon:hover > svg {
    height: 22px;
    width: 22px;
    color: white;
}
.delete-stage-icon:hover {
    background-color: #FF0000;
}

